body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*========== ISOLATION RESET ==========*/
/* La seccin de perfil tena conflictos de estilo con el resto de la app y no saba cmo arreglarlo.
   Al final encontr esta tcnica de "isolation" que bsicamente resetea todos 
   los estilos para un componente especfico. */
.component-isolation {
  all: initial;
  font-family: "Poppins", sans-serif; /* usamos Poppins en toda la app */
  color: var(--color-dark);
  box-sizing: border-box;
}

.component-isolation * {
  box-sizing: border-box;
}

/* Sistema de aislamiento de componentes basado en data-scope */
/* Esto lo hice despus de leer un artculo sobre CSS Scoping, me pareci
 ms limpio que usar clases para todo. As el perfil tiene su propios
 estilos sin afectar al resto */
[data-scope="perfil"] {
  all: initial;
  font-family: "Poppins", sans-serif;
  color: var(--color-dark);
  display: block;
  width: 100%;
}

[data-scope="perfil"] * {
  all: unset; /* Esto resetea todo primero */
  box-sizing: border-box;
  display: revert; /* luego devuelve los displays originales */
  font-family: inherit;
}

/* Tuve que restablecer manualmente los estilos para elementos especficos
   porque el 'all: unset' es demasiado agresivo. Seguro hay una forma ms
   elegante de hacer esto, pero ahora mismo funciona */
[data-scope="perfil"] h1,
[data-scope="perfil"] h2,
[data-scope="perfil"] h3 {
  display: block;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  font-weight: bold;
}

[data-scope="perfil"] p {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
}

[data-scope="perfil"] button {
  display: inline-block;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

[data-scope="perfil"] input,
[data-scope="perfil"] select,
[data-scope="perfil"] textarea {
  display: inline-block;
  font-family: inherit;
}
