.conocenos-container {
  display: flex;
  flex-direction: column;
  padding: 1.5rem 0rem;
  color: var(--color-primary);
  max-width: 1100px;
  margin: 3.5rem auto;
}

.conocenos-hero {
  background-color: rgba(255, 255, 255, 0.95);
  padding: 2rem;
  margin-top: 0rem;
  height: 6rem;
  border-radius: 14px 14px 0 0;
}

.conocenos-tagline {
  font-size: 1rem;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
  color: var(--color-text-light);
  margin-bottom: 1rem;
  text-align: center;
  position: relative;
  padding-bottom: 0.3rem;
}

.conocenos-title {
  text-align: center;
  font-size: 1.7rem;
  color: var(--color-dark);
  margin-top: 1rem;
  margin-bottom: 0.3rem;
  position: relative;
}

.conocenos-title:after {
  content: "";
  display: block;
  width: 50px;
  height: 3px;
  background: var(--color-primary);
  margin: 0.1rem auto;
}

.conocenos-subtitle {
  font-size: 1.2rem;
  color: var(--color-dark);
  margin-top: 0rem;
  margin-bottom: 0.5rem;
  border-left: 3px solid var(--color-primary);
  padding-left: 10px;
}

.conocenos-content {
  background-color: rgba(255, 255, 255, 0.95);
  padding: 4rem;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.05);
  display: flex;
  flex-direction: row;
  gap: 0.1rem;
  border-radius: 0 0 18px 18px;
  flex-wrap: wrap;
  align-content: space-between;
  justify-content: space-around;
}

.conocenos-content p {
  font-size: 0.95rem;
  margin-bottom: 0.8rem;
  color: var(--color-text-light);
  line-height: 1.5;
}

.conocenos-content p strong {
  color: var(--color-primary);
}

.conocenos-list {
  list-style-type: none;
  margin-left: 0;
  margin-bottom: 0.7rem;
}

.conocenos-list li {
  font-size: 0.95rem;
  margin-bottom: 0rem;
  color: var(--color-text-light);
  padding-left: 18px;
  position: relative;
  line-height: 1.3;
  display: flex;
  align-items: center;
  gap: 2px;
}

.conocenos-list li:before {
  content: "";
  color: var(--color-primary);
  font-size: 1.1rem;
  position: absolute;
  left: 0;
}

.conocenos-cta {
  font-size: 1rem;
  text-align: center;
  margin-top: 1rem;
  margin-bottom: 2rem;
  color: var(--color-primary);
  font-weight: bold;
}

.button-container {
  display: flex;
  justify-content: center;
  margin-top: 18px;
  margin-bottom: 12px;
}

.button-primary {
  height: 2.2rem !important;
  min-width: 7rem !important;
  background-color: var(--color-primary);
  color: white;
  font-size: 1rem !important;
  border: none;
  border-radius: 18px;
  padding: 0 1.2rem !important;
  box-shadow: 0 2px 6px rgba(230, 90, 80, 0.15);
  transition: all 0.3s ease;
}

.button-primary:hover {
  background-color: #e65a50;
  transform: translateY(-2px);
  box-shadow: 0 3px 8px rgba(230, 90, 80, 0.2);
}

/* Contenedor tipo Pinterest con composicin mejorada y distribucin alterna */
.conocenos-offer-container {
  position: relative;
  width: 100%;
  margin: 2rem 0;
  padding: 0 20px;
}

/* Responsive para mviles */
@media (max-width: 768px) {
  .conocenos-container {
    padding: 1rem 16px;
    margin: 2rem 12px;
  }

  .conocenos-hero {
    padding: 1.5rem 1rem;
    height: auto;
  }

  .conocenos-title {
    font-size: 1.4rem;
  }

  .conocenos-tagline {
    font-size: 0.9rem;
  }

  .conocenos-content {
    padding: 1.5rem 1rem;
    gap: 1rem;
  }

  .conocenos-content p {
    font-size: 0.9rem;
  }

  .conocenos-list li {
    font-size: 0.9rem;
  }

  .conocenos-offer-container {
    padding: 0 10px;
  }
}

/* Nuevo layout con distribucin ms orgnica */
.conocenos-cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 18px;
  width: 100%;
  max-width: 1100px;
  margin: 0 auto;
}

/* Estilos base para tarjetas e imgenes */
.conocenos-card,
.decorative-image-left,
.decorative-image-right,
.decorative-image-extra,
.decorative-image-bottom,
.decorative-image-final {
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.12);
  transition: all 0.3s ease;
  position: relative;
}

/* Estilos especficos para tarjetas */
.conocenos-card {
  background-color: #fff;
  padding: 1.4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
}

/* Contenedor para imgenes decorativas con mejor proporcin */
.image-container {
  position: relative;
  width: 100%;
  height: 280px;
  overflow: hidden;
  border-radius: 15px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.12);
  transition: all 0.3s ease;
  transform: translateZ(0); /* Forzar repintado */
  backface-visibility: hidden; /* Mejorar rendimiento */
  will-change: transform; /* Notificar al navegador sobre animaciones */
}

.decorative-image-left,
.decorative-image-right,
.decorative-image-extra,
.decorative-image-bottom,
.decorative-image-final {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transform: translateZ(0); /* Forzar recarga de imagen */
}

.image-container:nth-child(9) {
  height: 250px; /* Altura especfica para la imagen panormica */
}

.image-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.85);
  padding: 8px 12px;
  font-size: 0.85rem;
  color: var(--color-text);
  font-weight: 500;
  text-align: center;
  z-index: 2; /* Asegurar que est por encima de la imagen */
}

/* Efecto hover */
.conocenos-card:hover,
.image-container:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.image-container:hover .image-overlay {
  background-color: var(--color-primary);
  color: white;
}

.conocenos-card:nth-child(1) {
  grid-column: 1;
  grid-row: 1;
  background-color: #d7c36a29;
}

.image-container:nth-child(2) {
  grid-column: 2 / span 2;
  grid-row: 1;
}

.conocenos-card:nth-child(3) {
  grid-column: 1;
  grid-row: 2;
  background-color: #dd897c23;
}

.image-container:nth-child(4) {
  grid-column: 2;
  grid-row: 2;
}

.conocenos-card:nth-child(5) {
  grid-column: 3;
  grid-row: 2;
  background-color: #98b5a431;
}

.image-container:nth-child(6) {
  grid-column: 1;
  grid-row: 3;
}

.conocenos-card:nth-child(7) {
  grid-column: 2 / span 1;
  grid-row: 3;
  background-color: #6a66882a;
}

.image-container:nth-child(8) {
  grid-column: 3;
  grid-row: 3;
}

.image-container:nth-child(9) {
  grid-column: 1 / span 3;
  grid-row: 4;
  padding-bottom: 30%; /* Imagen panormica en la parte inferior */
}

.conocenos-quote {
  grid-column: 1 / span 3;
  grid-row: 5;
  text-align: center;
  font-style: italic;
  color: var(--color-primary);
  font-size: 1rem;
  height: 20px;
  margin: 1.5rem 0;
  border-top: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
}

.conocenos-quote::before {
  content: " ";
}

.conocenos-quote::after {
  content: " ";
}

/* Estilos de texto */
.conocenos-card h3 {
  color: var(--color-primary);
  margin-bottom: 0.8rem;
  font-size: 1.2rem;
  position: relative;
}

.conocenos-card h3::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 40px;
  height: 2px;
  background-color: var(--color-primary);
}

.conocenos-card p {
  font-size: 0.95rem;
  color: var(--color-text-light);
  margin-bottom: 0;
  line-height: 1.5;
}
